import { Form, Image } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../helper/function";
import { AuthService } from "../../services/authService";
import { MyFormItem } from "../core/Input";
import { MyBox } from "../core/MyBox";
import { MyButton } from "../core/MyButton";

export default function ChangeInfoComponent() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [address, setAddress] = useState('');
    const [avatar, setAvatar] = useState('');
    const [birthday, setBirthday] = useState(moment(new Date()));
    const [isLoading, setIsLoading] = useState(true);

    const loadData = useCallback(() => {
        setIsLoading(true);
        AuthService.GetProfile()
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setName(rs.data.name);
                    setEmail(rs.data.email);
                    setPhone(rs.data.phone);
                    setAddress(rs.data.address);
                    setAvatar(rs.data.avatar);
                    setBirthday(moment(rs.data.birthday));
                }
            })
            .finally(() => setIsLoading(false));
    }, []);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleSubmitForm() {
        setIsLoading(true);
        var body = {
            name: name,
            email: email,
            phone: phone,
            address: address,
            avatar: avatar,
            birthday: birthday
        };
        AuthService.UpdateProfile(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
            .finally(() => setIsLoading(false));
    }

    return (
        <MyBox
            isLoading={isLoading}
            title="Thay đổi thông tin cá nhân">
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />
                <MyFormItem.Input
                    label="Email"
                    value={email}
                    setValue={setEmail} />
                <MyFormItem.Input
                    label="Số điện thoại"
                    value={phone}
                    setValue={setPhone} />
                <MyFormItem.Input
                    label="Địa chỉ"
                    value={address}
                    setValue={setAddress} />
                <MyFormItem.Input
                    label="Ảnh đại diện"
                    value={avatar}
                    setValue={setAvatar} />
                <Image
                    height={50}
                    width={50}
                    src={avatar} />
                <MyFormItem.DatePicker
                    label="Ngày sinh"
                    value={birthday}
                    setValue={setBirthday} />
                <MyButton
                    value="Thay đổi"
                    type="success"
                    htmlType="submit" />
            </Form>
        </MyBox>
    );
}